<template>
  <div class="header">
    <mt-header fixed title="信纪元">
      <mt-button
        v-show="isShow"
        @click="handleBack"
        v-link="'/'"
        icon="back"
        slot="left"
        >返回</mt-button
      >
      <!-- <mt-button  @click="handleClose" icon="more" slot="right"></mt-button> -->
    </mt-header>
  </div>
</template>

<script>
export default {
  name: 'Header',
  data () {
    return {
      isShow: true
    }
  },
  watch: {
    // $route (now, old) { // 监控路由变换，控制返回按钮的显示
    //   if (now.path == '/home/home') {
    //     this.isShow = false
    //   } else {
    //     this.isShow = true
    //   }
    // }
  },
  methods: {
    handleBack () {
      alert(111)
      this.$router.back()
    }
  }
}
</script>



